/_ SOCIAL STUFF _/
.social-container {
  background: #eee;
  padding: 25px 5px;
}

a.social {
    margin: 0 1rem;
    transition: transform 250ms;
    display: inline-block;
  }

  a.social:hover {
    transform: translateY(-2px);
  }

  a.youtube {
    color: #eb3223;
  }
  
  a.facebook {
    color: #4968ad;
  }
  
  a.twitter {
    color: #49a1eb;
  }
  
  a.instagram {
    color: black;
  }

  a.linkedin {
    color: blue;
  }

  a.whatsapp {
    color: green;
  }

  a.skype {
    color: paleturquoise;
  }

